@import "../../App.scss";
.Work {
  margin-top: 5rem;
  
  // @include mobile {
  //   margin-bottom: 160rem;
  // }

  // @include desktop{
  //   margin-bottom: 15rem;
  // }

  .project_container {
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 1rem;
  }

  .project_item {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
  }

  .project_img {
    height: 250px;
    width: 100%;
    border-radius: 50px;
    object-fit: cover;
    vertical-align: middle;
    margin: 1rem 0 1.3rem 0;
  }

  .project_hover {
    background-color: var(--first-color);
    position: absolute;
    inset: 0;
    display: grid;
    place-items: center;
    opacity: 0;
    border-radius: 50px;
    transition: all 0.5s ease;
    margin: 1rem 0 1.3rem 0;
  }

  .project_item:hover .project_hover {
    opacity: 0.9;
  }

  .project_title {
    color: var(--text-white);
    font-size: var(--large-font-size);
    font-weight: var(--weight-500);
    transform: translateY(-20px);
    transition: all 0.5s ease;
  }

  .project_item:hover .project_title {
    transform: translateY(0);
  }

  .project_modal {
    position: fixed;
    inset: 0;
    background-color: hsla(0, 0%, 0%, 0.7);
    z-index: 1000;
    display: grid;
    place-items: center;
    padding: 15px;
  }

  .project_modal_content {
    background-color: var(--container-color);
    max-width: 700px;
    width: 85%;
    padding: 32px;
    border-radius: 20px;
    position: relative;
  }

  .modal_close {
    position: absolute;
    top: -3.5%;
    right: -8.5%;
    width: 50px;
    height: 50px;
    filter: invert(0.8);
  }

  .modal_title {
    text-align: center;
    color: var(--first-color);
    font-size: var(--h2-font-size);
    letter-spacing: 2px;
    margin-bottom: 24px;
  }

  .modal_list {
    text-align: center;
  }

  .modal_img {
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 30px;
  }
}
