.resume_icon {
  font-size: 25px;
}

.resume_item {
  position: relative;
  padding-left: 60px;
}

.resume_item::before {
  content: "";
  position: absolute;
  inset-block: 0;
  left: 20px;
  border-left: 1px solid var(--border-color);
}

.resume_icon {
  position: absolute;
  left: 0;
  top: 0;
  background-color: var(--first-color);
  color: var(--text-white);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
}

.resume_date {
  background-color: var(--container-color);
  padding: 2px 10px;
  border-radius: 20px;
  font-size: var(--tiny-font-size);
  font-weight: var(--weight-600);
}

.resume_subtitle {
  font-size: var(--large-font-size);
  font-weight: var(--weight-500);
  margin-block: 20px 10px;
}

.resume_subtitle span {
  color: var(--text-color);
  font-family: var(--body-font);
  font-size: var(--small-font-size);
  font-weight: var(--weight-600);
  position: relative;
  padding-left: 26px;
}

.resume_subtitle span::before {
  content: "";
  position: absolute;
  left: 7px;
  top: 9px;
  width: 10px;
  height: 2px;
  background-color: var(--text-color);
}

.resume_grade {
  font-size: var(--small-font-size);
  line-height: 1.6;
}

.resume{
    width: 80%;
    display: flex;
    justify-content: center;
    margin: auto;
}

.resume_head h3{
    color: var(--first-color) !important;
    font-size: 3rem;
    margin: 1.5rem 0;
}

.resume_data{
    display: flex;
    flex-direction: column;
    justify-content: center;
}