.style_switcher {
    position: fixed;
    left: 0;
    top: 10%;
    width: 220px;
    padding: 15px;
    box-shadow: 0 0 4pc hsl(0, 0%, 80%);
    z-index: 10;
    transform: translateX(-100%);
    transition: all 0.3s ease;
}

.show_switcher{
    transform: translateX(0);
}

.style_switcher,
.style_switcher_toggle,
.theme_toggle {
    border-radius: 0 5px 5px 0;
}

.style_switcher_item {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 4px;
    row-gap: 8px;
}


.style_switcher_title,
.style_switcher_toggle,
.theme_toggle,
.style_switcher_close {
    color: hsl(0, 0%, 40%);
    background-color: transparent !important;

}

.style_switcher_title {
    font-size: var(--small-font-size);
    font-weight: var(--weight-600);
    // border-bottom: 1px solid hsl(0. 0%, 87%);
    padding-bottom: 5px;
    margin-bottom: 15px;
}

.theme_img {
    width: 30px;
    cursor: pointer;
}

.style_switcher_toggle,
.theme_toggle {
    position: absolute;
    left: 94%;
    background-color: var(--text-white);
    height: 50px;
    width: 50px;
    font-size: var(--large-font-size);
    cursor: pointer;
    display: grid;
    place-items: center;
    box-shadow: 0 0 2px hsl(0, 0%, 80%);
    transition: all 0.3s ease;
}

.show_switcher .style_switcher_toggle,
.show_switcher .theme_toggle{
    left: -100%;
}

.style_switcher_toggle{
    top: 10%;
}

.theme_toggle{
    bottom: 10%;
}

.style_switcher_close{
    cursor: pointer;
    font-size: var(--h2-font-size);
    position: absolute;
    top: 2%;
    right: 25px;
}